import { KakaoConstants, DrawingManagerOptions, KakaoDrawingEvents } from './../basic/kakao-maps.constants';
import { LatLng } from '../basic/kakao-maps.LatLng';
import { LatLngBounds } from '../basic/kakao-maps.LatLngBounds';

declare var daum: any;

export class DrawingManager {
  private object: any;
  constructor(options: DrawingManagerOptions) {
    return (this.object = new daum.maps.drawing.DrawingManager(options));
  }

  setStyle(type: any, prop: string, value: string|number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setStyle(type, prop, value);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setStrokeWeight(strokeWeight: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setStrokeWeight(strokeWeight);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setStrokeColor(strokeColor: string): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setStrokeColor(strokeColor);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  select(type: any, index:number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.select(type, index);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  cancel() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.cancel();
  }

  undo() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.undo();
  }

  redo() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.redo();
  }

  undoable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.undoable();
  }

  redoable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.redoable();
  }

  getData(types: any[]) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getData(types);
  }

  getOverlays(types: any[]) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getOverlays(types);
  }

  put(overlayType, param1:LatLng | LatLngBounds | LatLng[]| (LatLng[])[], param2?:number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.put(overlayType, param1, param2);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  remove(overlay: any) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.remove(overlay);
  }

  addListeners(types: KakaoDrawingEvents[], handler?) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    if(types == null || types.length == 0){
      throw new Error('events array is null or empty.');
    }
    for(let type of types){
      this.object.addListener(type, (res)=> {
        if(handler){
          handler({type:type, response:res});
        }
      });
    }
  }

  addListener(type: KakaoDrawingEvents, handler?) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
   
    this.object.addListener(type, (res)=> {
      if(handler){
        handler({type:type, response:res});
      }
    });
  }

}
