import { KakaoConstants } from './../basic/kakao-maps.constants';
import { DrawingManager } from './kakao-maps.DrawingManager';

declare var daum: any;

export class Toolbox {
  private object: any;
  constructor(options: {drawingManager: DrawingManager}) {
    return (this.object = new daum.maps.drawing.Toolbox(options));
  }

  getElement() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getElement();
  }

}
