import { KakaoConstants } from "./kakao-maps.constants";
import { TilesetCopyright } from "./kakao-maps.TilesetCopyright";

declare var daum: any;

export class Tileset {

  private object: any;
  constructor(width:number, height:number, urlFunc?, copyright?:TilesetCopyright[], dark?:boolean, minZoom?:number, maxZoom?:number){
    return this.object = new daum.maps.Tileset(width, height, urlFunc, copyright, dark, minZoom, maxZoom);
  }

  add(id: string, tileset: Tileset): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.APIKEY_ERROR);
        }
        this.object.add(id, tileset);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }
}