import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { KakaoMapsProvider } from './../kakao-maps.provider';

@Component({
  selector: 'kakao-maps',
  template: `<div id="kakaomaps-div" [ngStyle]="mapConfig"></div>`,
  styles: [``],
})
export class KakaoMapsComponent implements OnInit {
  @Input() mapConfig: MapConfig = { width: '50%', height: '50%' };

  constructor(public _kakaoMapsProvider: KakaoMapsProvider) {}

  ngOnChanges(changes: SimpleChanges) {
    this._kakaoMapsProvider.setContainerStyleConfig(
      changes.mapConfig.currentValue.width,
      changes.mapConfig.currentValue.height
    ).then((res)=>{

    }, (err)=>{}).catch(()=>{

    });
  }
  ngOnInit() {}

  ngAfterViewInit() {}

  setMapConfig(width: number, height: number) {
    this.mapConfig.width = width + 'px';
    this.mapConfig.height = height + 'px';
    this._kakaoMapsProvider.setContainerStyleConfig(this.mapConfig.width, this.mapConfig.height);
  }

  getMapConfig() {
    let config = {
      width: Number(this.mapConfig.width.replace('px', '')),
      height: Number(this.mapConfig.height.replace('px', '')),
    };

    return config;
  }
}

export interface MapConfig {
  width: string;
  height: string;
}
