import { KakaoConstants, RectangleClassOptions, RectangleOptions } from "./kakao-maps.constants";
import { LatLng } from "./kakao-maps.LatLng";
import { KakaoMap } from "./kakao-maps.KakaoMap";
import { LatLngBounds } from "./kakao-maps.LatLngBounds";

declare var daum: any;

export class Rectangle {

  private object: any;
  constructor(options: RectangleClassOptions){
    return this.object = new daum.maps.Rectangle(options);
  }

  setMap(map: KakaoMap): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

  setZIndex(zIndex: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setZIndex(zIndex);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getZIndex() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getZIndex();
  }

  setOptions(options: RectangleOptions ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setOptions(options);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setBounds(bounds: LatLngBounds ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setBounds(bounds);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getBounds() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getBounds();
  }

}