import { KakaoConstants, PolylineOptions, PolylineClassOptions } from "./kakao-maps.constants";
import { LatLng } from "./kakao-maps.LatLng";
import { KakaoMap } from "./kakao-maps.KakaoMap";

declare var daum: any;

export class Polyline {

  private object: any;
  constructor(options: PolylineClassOptions){
    return this.object = new daum.maps.Polyline(options);
  }

  setMap(map: KakaoMap): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

  setZIndex(zIndex: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setZIndex(zIndex);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getZIndex() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getZIndex();
  }

  setOptions(options: PolylineOptions ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setOptions(options);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setPath(path: LatLng[] ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setPath(path);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getPath() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getPath();
  }

  getLength() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getLength();
  }
}