import { KakaoConstants } from "./kakao-maps.constants";
import { Coords } from "./kakao-maps.Coords";

declare var daum: any;

export class LatLng {

  private object: any;
  constructor(latitude: number, longitude: number){
    return this.object = new daum.maps.LatLng(latitude, longitude);
  }

  getLat(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.getLat();
  }

  getLng(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.getLng();
  }

  equals(latlng: LatLng){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.equals(latlng);
  }

  toString(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.toString();
  }

  toCoords(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    let res:Coords = this.object.toCoords()
    return res;
  }
}