import { KakaoConstants } from './kakao-maps.constants';
import { Coords } from './kakao-maps.Coords';
import { LatLng } from './kakao-maps.LatLng';

declare var daum: any;

export class RoadviewClient {
  private object: any;
  constructor() {
    return (this.object = new daum.maps.RoadviewClient());
  }

  getNearestPanoId(position:LatLng, radius:number, callback) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getNearestPanoId(position, radius, (res)=> {
      if(callback){
        callback(res);
      }
    });
  }
}
