import { KakaoConstants, StaticMapOptions } from "./kakao-maps.constants";
import { LatLng } from "./kakao-maps.LatLng";
import { KakaoMap } from "./kakao-maps.KakaoMap";
import { Roadview } from "./kakao-maps.Roadview";
import { Viewpoint } from "./kakao-maps.Viewpoint";

declare var daum: any;

export class StaticMap {

  private object: any;
  constructor(container, options: StaticMapOptions){
    return this.object = new daum.maps.StaticMap(container, options);
  }

  setCenter(latlng: LatLng): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setCenter(latlng);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getCenter() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getCenter();
  }

  setLevel(level: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setLevel(level);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getLevel() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    let res:LatLng = this.object.getLevel();
    return res;
  }

  setMapTypeId(mapTypeId: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMapTypeId(mapTypeId);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMapTypeId() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    let res:LatLng = this.object.getMapTypeId();
    return res;
  }
}