import { KakaoMap } from './kakao-maps.KakaoMap';
import { KakaoConstants } from './kakao-maps.constants';

declare var daum: any;

export abstract class AbstractOverlay {
    private object: any;
    constructor(){
      return this.object = new daum.maps.AbstractOverlay;
    }
  abstract draw();

  abstract onAdd();

  abstract onRemove();

  setMap(map: KakaoMap): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

  getPanels() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getPanels();
  }

  getProjection() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getProjection();
  }
}
