import { KakaoConstants, MarkerClustererOptions } from './../basic/kakao-maps.constants';
import { Marker } from '../basic/kakao-maps.Marker';

declare var daum: any;

export class MarkerClusterer {
  private object: any;
  constructor(options?: MarkerClustererOptions) {
    return (this.object = new daum.maps.MarkerClusterer(options));
  }

  addMarker(marker:Marker, nodraw?: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.addMarker(marker, nodraw);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  addMarkers(markers:Marker[], nodraw?: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.addMarker(markers, nodraw);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  removeMarker(marker:Marker, nodraw?: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.removeMarker(marker, nodraw);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  removeMarkers(markers:Marker[], nodraw?: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.removeMarkers(markers, nodraw);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  clear() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.clear();
  }

  redraw() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.redraw();
  }

  setGridSize(size:number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setGridSize(size);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getGridSize() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getGridSize();
  }

  setMinClusterSize(size:number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMinClusterSize(size);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMinClusterSize() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getMinClusterSize();
  }

  setAverageCenter(bool:boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setAverageCenter(bool);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getAverageCenter() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getAverageCenter();
  }

  setMinLevel(level:number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMinLevel(level);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMinLevel() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getMinLevel();
  }

  setTexts(texts:any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setTexts(texts);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getTexts() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getTexts();
  }

  setCalculator(calculator:any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setCalculator(calculator);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getCalculator() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getCalculator();
  }

  setStyles(styles:any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setStyles(styles);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getStyles() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.getStyles();
  }
}
