import { KakaoConstants, KeywordSearchOptions, KakaoCategoryCode } from './../basic/kakao-maps.constants';
import { KakaoMap } from '../basic/kakao-maps.KakaoMap';

declare var daum: any;

export class Places {
  private object: any;
  constructor(map?: KakaoMap) {
    return (this.object = new daum.maps.services.Places(map));
  }

  setMap(map: KakaoMap): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  keywordSearch(keyword:string, callback, options: KeywordSearchOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.keywordSearch(keyword, callback, options);
  }

  categorySearch(code: KakaoCategoryCode, callback, options: KeywordSearchOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.categorySearch(code, callback, options);
  }
}
