import { KakaoConstants, MarkerOptions } from "./kakao-maps.constants";
import { Coords } from "./kakao-maps.Coords";
import { LatLng } from "./kakao-maps.LatLng";
import { KakaoMap } from "./kakao-maps.KakaoMap";
import { Viewpoint } from "./kakao-maps.Viewpoint";
import { MarkerImage } from "./kakao-maps.MarkerImage";
import { Roadview } from "./kakao-maps.Roadview";

declare var daum: any;

export class Marker {

  private object: any;
  constructor(options: MarkerOptions){
    return this.object = new daum.maps.Marker(options);
  }

  setMap(map_or_roadview: KakaoMap | Roadview): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map_or_roadview);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

  setImage(image: MarkerImage ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setImage(image);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getImage() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getImage();
  }

  setPosition(position: LatLng | Viewpoint ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setPosition(position);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getPosition() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    let res:LatLng = this.object.getPosition();
    return res;
  }

  setZIndex(zIndex: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setZIndex(zIndex);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getZIndex() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getZIndex();
  }

  setVisible(visible: boolean ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setVisible(visible);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getVisible() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getVisible();
  }

  setTitle(title: string ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setTitle(title);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getTitle() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getTitle();
  }

  setDraggable(draggable: boolean ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setDraggable(draggable);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getDraggable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getDraggable();
  }

  setClickable(clickable: boolean ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setClickable(clickable);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getClickable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getClickable();
  }

  setAltitude(altitude: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setAltitude(altitude);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getAltitude() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getAltitude();
  }

  setRange(range: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setRange(range);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getRange() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getRange();
  }

  setOpacity(opacity: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setOpacity(opacity);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getOpacity() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getOpacity();
  }
}