import { KakaoConstants, AddressSearchOptions, Coord2AddressOptions, Coord2RegionCodeOptions } from './../basic/kakao-maps.constants';
import { KakaoMap } from '../basic/kakao-maps.KakaoMap';

declare var daum: any;

export class Geocoder {
  private object: any;
  constructor() {
    return (this.object = new daum.maps.services.Geocoder());
  }

  addressSearch(addr: string, callback, options: AddressSearchOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.addressSearch(addr, callback, options);
  }

  coord2Address(x:number, y:number, callback, options: Coord2AddressOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.coord2Address(x, y, callback, options);
  }

  coord2RegionCode(x:number, y:number, callback, options: Coord2RegionCodeOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.coord2RegionCode(x, y, callback, options);
  }

  transCoord(x:number, y:number, callback, options: Coord2RegionCodeOptions) {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.transCoord(x, y, callback, options);
  }
}
