import { KakaoConstants, RoadviewOptions } from './kakao-maps.constants';
import { Coords } from './kakao-maps.Coords';
import { LatLng } from './kakao-maps.LatLng';
import { Viewpoint } from './kakao-maps.Viewpoint';

declare var daum: any;

export class Roadview {
  private object: any;
  constructor(container: any, options: RoadviewOptions) {
    return (this.object = new daum.maps.Roadview(container, options));
  }

  setPanoId(panoId: number, position: LatLng): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setPanoId(panoId, position);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getPanoId() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getPanoId();
  }

  setViewpoint(viewpoint: Viewpoint): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setViewpoint(viewpoint);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getViewpoint() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getViewpoint();
  }

  getViewpointWithPanoId() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getViewpointWithPanoId();
  }

  getPosition() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getPosition();
  }

  relayout() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    this.object.relayout();
  }

}
