import { KakaoConstants, MapOptions, LevelOptions, MapTypeId, CopyrightPosition } from './kakao-maps.constants';
import { LatLng } from './kakao-maps.LatLng';
import { LatLngBounds } from './kakao-maps.LatLngBounds';
import { MapTypeControl } from './kakao-maps.MapTypeControl';
import { ZoomControl } from './kakao-maps.ZoomControl';

declare var daum: any;

export class KakaoMap {
  private object: any;
  constructor(container: any, options: MapOptions) {
    return (this.object = new daum.maps.Map(container, options));
  }

  setCenter(latlng: LatLng): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setCenter(latlng);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getCenter() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getCenter();
  }

  setLevel(level: number, options?: LevelOptions): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        if (options == null) {
          this.object.setLevel(level);
        } else {
          this.object.setLevel(level, options);
        }
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getLevel() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getLevel();
  }

  setMapTypeId(mapTypeId: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMapTypeId(mapTypeId);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMapTypeId() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMapTypeId();
  }

  setBounds(
    bounds: LatLngBounds,
    paddingTop?: number,
    paddingRight?: number,
    paddingBottom?: number,
    paddingLeft?: number
  ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        if(paddingLeft){
          this.object.setBounds(bounds, paddingTop, paddingRight, paddingBottom, paddingLeft);
        }else if(paddingBottom){
          this.object.setBounds(bounds, paddingTop, paddingRight, paddingBottom);
        }else if(paddingRight){
          this.object.setBounds(bounds, paddingTop, paddingRight);
        }else if(paddingTop){
          this.object.setBounds(bounds, paddingTop);
        }else{
          this.object.setBounds(bounds);
        }
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getBounds() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getBounds();
  }

  setMinLevel(minLevel: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMinLevel(minLevel);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setMaxLevel(maxLevel: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMaxLevel(maxLevel);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  panBy(dx: number, dy: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.panBy(dx, dy);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  panTo(latlng_or_bounds: LatLng | LatLngBounds, padding: number): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        if(padding){
          this.object.panTo(latlng_or_bounds, padding);
        }else{
          this.object.panTo(latlng_or_bounds);
        }
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  addControl(control: MapTypeControl | ZoomControl, position: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.addControl(control, position);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  removeControl(control: MapTypeControl | ZoomControl): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.removeControl(control);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setDraggable(draggable: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setDraggable(draggable);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getDraggable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getDraggable();
  }

  setZoomable(zoomable: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setZoomable(zoomable);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getZoomable() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getZoomable();
  }


  relayout(): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.relayout();
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  addOverlayMapTypeId(mapTypeId: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.addOverlayMapTypeId(mapTypeId);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  removeOverlayMapTypeId(mapTypeId: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.removeOverlayMapTypeId(mapTypeId);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setKeyboardShortcuts(active: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setKeyboardShortcuts(active);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getKeyboardShortcuts() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getKeyboardShortcuts();
  }

  setCopyrightPosition(copyrightPosition: CopyrightPosition, reversed: boolean): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        if(reversed){
          this.object.setCopyrightPosition(copyrightPosition, reversed);
        }else{
          this.object.setCopyrightPosition(copyrightPosition);
        }
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getProjection() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getProjection();
  }

  setCursor(style: string): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setCursor(style);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }
}
