import { KakaoConstants } from "./kakao-maps.constants";
import { LatLng } from "./kakao-maps.LatLng";

declare var daum: any;

export class LatLngBounds {

  private object: any;
  constructor(sw: LatLng, ne: LatLng){
    return this.object = new daum.maps.LatLngBounds(sw, ne);
  }

  getSouthWest(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    let res:LatLng = this.object.getSouthWest();
    return res;
  }

  getNorthEast(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    let res:LatLng = this.object.getNorthEast();
    return res;
  }

  equals(latlngBounds: LatLngBounds){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.equals(latlngBounds);
  }

  toString(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.toString();
  }

  isEmpty(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.isEmpty();
  }

  extend(latlng: LatLng){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.extend(latlng);
  }

  contain(latlng: LatLng){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.contain(latlng);
  }
}