import { KakaoConstants, RoadviewOptions } from './kakao-maps.constants';
import { Coords } from './kakao-maps.Coords';
import { LatLng } from './kakao-maps.LatLng';
import { KakaoMap } from './kakao-maps.KakaoMap';

declare var daum: any;

export class RoadviewOverlay {
  private object: any;
  constructor() {
    return (this.object = new daum.maps.RoadviewOverlay());
  }

  setMap(map: KakaoMap): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

}
