import { KakaoConstants } from "./kakao-maps.constants";
import { TilesetCopyright } from "./kakao-maps.TilesetCopyright";
import { LatLng } from "./kakao-maps.LatLng";

declare var daum: any;

export class Point {

  private object: any;
  constructor(x:number, y:number){
    return this.object = new daum.maps.Point(x, y)
  }

  equals(point: Point){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.equals(point);
  }

  toString(){
    if (this.object == null) {
      throw new Error(KakaoConstants.APIKEY_ERROR);
    }
    return this.object.toString();
  }

}