import { KakaoConstants, CustomOverlayOptions } from "./kakao-maps.constants";
import { LatLng } from "./kakao-maps.LatLng";
import { KakaoMap } from "./kakao-maps.KakaoMap";
import { Roadview } from "./kakao-maps.Roadview";
import { Viewpoint } from "./kakao-maps.Viewpoint";

declare var daum: any;

export class CustomOverlay {

  private object: any;
  constructor(options: CustomOverlayOptions){
    return this.object = new daum.maps.CustomOverlay(options);
  }

  setMap(map_or_roadview: KakaoMap | Roadview): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setMap(map_or_roadview);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getMap() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getMap();
  }

  setPosition(position: LatLng | Viewpoint ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setPosition(position);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getPosition() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    let res:LatLng = this.object.getPosition();
    return res;
  }

  setContent(content: any): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setContent(content);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getContent() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    let res:LatLng = this.object.getContent();
    return res;
  }

  setZIndex(zIndex: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setZIndex(zIndex);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  setVisible(visible: boolean ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setVisible(visible);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getVisible() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getVisible();
  }

  getZIndex() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getZIndex();
  }

  setAltitude(altitude: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setAltitude(altitude);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getAltitude() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getAltitude();
  }

  setRange(range: number ): Promise<any> {
    return new Promise((resolve, reject) => {
      try {
        if (this.object == null) {
          throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
        }
        this.object.setRange(range);
        resolve();
      } catch (e) {
        reject(e);
      }
    });
  }

  getRange() {
    if (this.object == null) {
      throw new Error(KakaoConstants.MAP_OBJECT_INIT_ERROR);
    }
    return this.object.getRange();
  }
}